
#Rulemgrcanvas{
    background-color: black;
}

#modelpageHeader{
    font-size: 24px;
    color:rgb(129, 255, 244);
    margin-top: 5px;
}

#rulemodimport{
    margin-left: 20px;
    display: flex;
}

#modelselect{
    border: 1px solid black;
    background-color: darkgray;
    margin-bottom: 5px;
}

.rulemodel_importbtn {
    border-radius: 5px;
    background-color: rgb(200, 248, 250);
    width: 300px;
    height: 40px;
    margin-left: 50px;
    margin-top: 3px;
}

.rulemodel_importbtn:hover {
    transition: .2s;
    background-color: rgb(239, 250, 200);
    border: 0px solid yellow;
    box-shadow:
        -1px 1px 10px #ebe7af,
        1px 1px 4px #ebe7af,
        -1px -1px 4px #ebe7af,
        1px 1px 4px #ebe7af;
}

#RuleModelSelector{
    margin: 5px;
    width: 100%;
    display: flex;
    padding: 10px;

}

#NewRuleModelForm {
    background-color: azure;
    padding: 5px;
    box-shadow:
        -1px 1px 10px #263c52,
        1px 1px 4px #263c52,
        -1px -1px 4px #263c52,
        1px 1px 4px #263c52;
}

#RuleModelddown{
    margin-left: 20px;
    background-color: azure;
    padding: 5px;
    box-shadow:
        -1px 1px 10px #263c52,
        1px 1px 4px #263c52,
        -1px -1px 4px #263c52,
        1px 1px 4px #263c52;
}

#Modelnamediv {
    font-size: 22px;
    letter-spacing: 1px;
    background-color: rgb(118, 176, 196);
    color: white;
    width: 400px;
    margin:auto;
    padding: 5px;
    padding-left: 10px;
    border-radius: 0px 0px 5px 5px;
    margin-top: 5px;
    margin-bottom: 5px;
    box-shadow:
        2px 2px 5px #263c52,
        -2px -2px 5px #263c52;
}

#addchangeselect{
    margin: auto;
    height: 60px;
    width: 90%;
    padding: 5px;
    display: flex;
}
#btnaddchg{
    height: 30px;
}

#multbutton{
    height: 30px;
}

#ruleWorkspace {
    background-color: white;
    width: 60%;
    margin: auto;
}

#existingtitle{
    display: inline;
    width: 500px;
    padding: 5px;
    padding-right: 10px;
}


#submitruleform{
    margin: auto;
    width: 400px;
}

.rulesubmitbtn {
    width: 300px;
    margin: auto;
    height: 50px;
    background-color: rgb(218, 249, 255);
}

#ruledelete_btn{
    margin-left: 10px;
}