.fieldsdiv {
    background-color: rgb(196, 186, 186);
    grid-column: 2/2;
    grid-row:4/4;
    border: 1px solid black;
    border-radius: 10px;
    padding: 5px;
    height: 300px;
    margin-left: 100px;
    margin-right: 100px;
    overflow: hidden;
    transition: .7s ease-in;
    font-family: Arial, Helvetica, sans-serif;
}

#fieldmodalheader{
    margin-bottom: 5px;
    font-size: 18px;
}