

.logoutContainer {
	display: grid;
	grid-template-columns: 20% 50% 20%;
	grid-column-gap: 20px;
	grid-template-rows: 800px;
	background: linear-gradient(to top, #262d64, #FFFFFF);
	font-family: Helvetica;
}


.logout-button {
	margin: 3px;
	border-radius: 20px;
	border: none;
	background-color: #262d64;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
}
