
#rulebox{
    background-color: rgb(219, 219, 219);
    padding: 5px;
}

#FldModal1{
    margin: auto;
    height: 500px;
    width: 500px;
    display: grid;
    grid-template-rows: 100px 400px;
    grid-template-columns: 200px 200px;
    column-gap: 50px;
    
}

#rulemultpicker{
    display: flex;
    height: 60px;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: rgb(195, 227, 248);
    width: 300px;
    box-shadow:
        -2px 2px 4px #263c52,
        2px 2px 4px #263c52,
        -2px -2px 4px #263c52,
        2px 2px 4px #263c52;
}

.fieldsecret{
    height:0px;
    overflow: hidden;
}

.fldddown1 {
    grid-column: 1/1;
    grid-row: 1/1; 
}


.fldddown2 {
    grid-column: 2/2;
    grid-row: 1/1; 
}

.fldlist_itms {
    overflow: scroll;
    border: 1px solid black;
    margin-left: 10px;
    padding: 3px;
    background: white;
}

#fldlist1 {
    grid-column: 1/1;
    grid-row: 2/2;
}
#fldlist2 {
    grid-column: 2/2;
    grid-row: 2/2;
}

.fieldheader{
    background-color: rgb(109, 161, 177);
    color: white;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 5px;
}

.fieldddown {
    width: 150px;
    margin-left: 30px;
    font-family:Helvetica;
}

.fldlst_scroll {
    margin: auto;
    font-family:Helvetica;
}

.andor_btn {
    background: white;
}

.valuediv {
    background: white;
    cursor:pointer;
}



#exceptionheader{
    margin: 10px;
    font-size: 18px;
    width: 150px;

}

#andordiv{
    margin-top: 10px;
}

#multbutton{
    width: 100px;
    background-color: rgb(218, 249, 255);
    border-radius: 5px;
}
.andor_btn{
    width: 50px;
    border-radius: 5px;
}