

.loginContainer {
	display: grid;
	grid-template-columns: 20% 50% 20%;
	grid-column-gap: 20px;
	grid-template-rows: 800px;
	background: linear-gradient(to top, #FFFFFF, #6921bc);
	font-family: Helvetica;
}

#loginbox {
	grid-column:2/3;
	grid-row: 1/1;
	margin-top: 200px;
	margin-bottom: 200px;
	background-color: #FFFFFF;
	overflow: hidden;
	border-radius: 10px;
	box-shadow: 0 10px 20px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
	text-align: center;
	z-index: 1;
}

#formbox {
	margin: 0 auto;
	margin-top: 70px;
	letter-spacing: 0.5px;
	line-height: 20px;
	border: 1px solid #DDDDDD;
	background-color: #FFFFFF;
	text-align: center; 
	width: 50%;
	justify-content: center;
	border-radius: 10px;
}

#logintitle {
	letter-spacing: .40rem;
	font-size: 34px;
	margin-top: 50px;
	font-family: system-ui;
}

.log-input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.login-button {
	margin: 3px;
	border-radius: 20px;
	border: none;
	background-color: #693dda;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
}

.login-button:hover {
	filter: brightness(1.5);
	transition: .6s
}

#signuplinkbox {
	font-size: 10px;
	text-align: left;
}