#importfull{
    display: grid;
    grid-template-columns: 10% 70% 20%;
    grid-column-gap: 20px;
    grid-template-rows: 10% 10% 35% 20% 45%;
    grid-row-gap: 10px;
    height: 800px;
    background: linear-gradient(to top, #FFFFFF, #7c5c21);
    font-family: Helvetica;
}

#empimportHeader {
    grid-column: 2/2;
    grid-row: 2/2;
    text-align: center;
    font-size: 34px;
    color: seashell;
}

#empimportContainer{
    grid-column: 2/2;
    grid-row: 3/3;
    display: grid;
    margin:auto;
    grid-template-columns: 400px 400px;
    grid-template-rows: 100%;
    width: 830px;
    grid-column-gap: 20px;
    border-radius: 10px;
    background-color: black;
}

.empimportelem {
    background-color: rgb(231, 238, 252);
    margin: 10px 10px 10px 10px;
    border-radius: 5px;    
    padding: 10px;
    box-shadow:
        inset 4px 4px 8px #1f3449,
        inset -6px -8px 12px #ffffff;
    text-align: center;
}

.empheader {
    font-family: Cambria;
    text-align: center;
    font-size: 20px;
    color: #260955
}

.empsubmitbtn {
    height: 35px;
    width: 140px;
    margin: auto;
	border-radius: 5px;
	border: none;
	background-color: #161755;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 0px 20px;
	letter-spacing: 1px;
	text-transform: uppercase;
}


.empfilebtn {
    height: 35px;
    width: 250px;
	margin: 0px 0px 3px 0px ;
	border-radius: 0px 5px 5px 0px;
	border: none;
	background-color: #161755;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 4px 3px;
	letter-spacing: 1px;
    overflow: hidden;
}

