
#egraphContainer {
    display: grid;
    grid-template-columns: 20% 60% 20%;    
    grid-template-rows: 40px 800px;
}


.fieldelem {
    height: '40px';
    border: 1px solid black;
    display: flex;
    margin-bottom: 1px;
}

.fieldclick {
    width: 170px;
}

.fieldclick_xy{
    border: 1px solid rgb(146, 32, 32);
    padding: 3px;
    width: 180px;
    margin-left: 10px;
    border-radius: 3px;
    cursor: pointer;
    height: 40px;

}

.fieldclick_xy {
    transition: .3s ease-in;
}

#charttitle {
    margin: auto;
    font-family:Cambria;
    letter-spacing: 1px;
    font-size: 24px;
}

#graphsidebar {
    grid-row: 1/3;
    grid-column: 1/1;
    border: 1px solid black;
    border-radius: 0px 10px 10px 0px;
    margin-right: 10px;
}

.sidenav {
    display: flex; /* Will be hidden on mobile */
    flex-direction: column;
    grid-area: sidenav;
    background-color: #394263;
}

.sidenav__list {
	padding: 0;
	margin-top: 10px;
	list-style-type: none;
}

.sidenav__list-item {
	padding: 10px 10px 10px 20px;
	color: #ddd;
    text-align: left;
}

.sidenav__list-item:hover {
	background-color: rgba(255, 255, 255, 0.2);
	cursor: pointer;
}


#graphsidebarHeader{
    color:blanchedalmond;
    margin-left: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
    letter-spacing: 2.4px;
    width: 200px;
    text-align: left;
    font-size: 18px;
    height: 80px;
    overflow: hidden;
}

.TopElement{
    color: white;
    font-size: 18px;
    padding: 5px;
    height:50px;
    cursor: pointer;
    margin-top: 3px;
}
.TopElement:hover{
    transition: .4s;
    background-color: rgb(180, 164, 201);
}

#sidebtnCreate{
    background-color: #454e74;
}
.bar {
    border-radius: 3px;
}

.closemodalbtn{
    width: 90%;
    margin-left: 5%;
    background-color: #394263;
    color: white;
}
.closemodalbtn:hover{
    transition: .1s;
    background-color: #293563;
}

#activemodelheader{
    font-size: 18px;
    font-size: bold;
    letter-spacing: 0px;
}

.fldheader{
    margin: auto;
}

.chartclick{
    background-color: rgb(240, 240, 240);
    margin: auto;
    margin-top:5px;
    margin-bottom: 5px;
    width: 200px;
    padding: 7px;
    border: 1px groove black;
    border-radius: 20px 7px 20px 7px;
    cursor: pointer;
    text-align: center;
}

.textclass1 {
    width: 10px;
    color: red;
}

#mdl_report{
    height: 200px;
}
#mdl_datespread{
    height: 200px;
}
#mdl_rule{
    background-color: rgb(231, 225, 230);
    padding: 5px;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 300px;
    text-align: center;
}

.modalemp_header{
    margin: 5px;
    font-family: helvetica;
    letter-spacing: .3em;
}
.modalemp_ddown{
    width: 250px;
    margin: 5px;
}

#mdl_visuals{
    height: 300px;
    width: 600px;
    display: grid;
    grid-template-columns: 25% 75%;
    padding: 5px;
}
.colorpicker{
    width: 120px;
    background-color: rgb(238, 238, 238);
    margin-top:5px;
    margin-left:2px;
    border: 1px solid black;
    border-radius: 5px;
    padding-left: 10px;
}
.colorpicker:hover{ 
    transition: 1.0s;
    box-shadow:
        -1px 1px 10px #48afb3,
        1px 1px 4px #48afb3,
        -1px -1px 4px #48afb3,
        1px 1px 4px #48afb3;
}