
#depttable_canvas{
    margin-top: 10px;
    display: grid;
    grid-template-columns: 15% 70% 15%;
    grid-template-rows: 200px 200px 500px;
}

#depttitle {
    font-size: 24px;
}

#deptimportcontent{
    margin: auto;
    padding: 15px;
    grid-column: 2/2;
    grid-row: 1/1;
    background-color: rgb(219, 219, 236);
    border-radius: 4px;
}

#depttable1{
    grid-column: 2/2;
    grid-row: 2/2;
    margin: auto;
}

.deptfilebtn{
    text-align: center;
    background-color: blanchedalmond;
}

#deptupl_div{
    height: 15px;
}